import React, { Component } from "react";
import "../Styles/MealDetails.css"
import { ProgressBar, wait } from "../dependencies";
import { userData, data } from "../Data";

export default class MealDetails extends Component {
  componentDidMount() {
    this.introPageTransition();
    this.addProgressBars();
  }

  introPageTransition = async () => {
    const page = document.getElementById("mealDetailsPage");
    const pageHeader = document.getElementById("mealDetailsPageHeader");

    page.style.cssText = `position: fixed; bottom: 50%; left: 50%; opacity: 0; transform: translate(-50%, 100%); transition: bottom 200ms`;
    pageHeader.style.position = "relative";

    await wait(10);
    page.style.bottom = "100%";
    page.style.opacity = "1";

    await wait(200);
    page.removeAttribute("style");
    pageHeader.removeAttribute("style");
  };

  sumNutrition = (n) => {
    const getDataByDate = userData.diary.filter((e) => e.date === this.props.date)[0];
    const getDataById = getDataByDate[this.props.mealName].map(
      (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
    );
    return getDataById.map((e) => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
  };

  fatPercent = () =>
    Math.min(Math.max(Math.round(((this.sumNutrition("fat") * 9) / this.sumNutrition("kcal")) * 100), 0), 100);

  proteinPercent = () =>
    Math.min(Math.max(Math.round(((this.sumNutrition("protein") * 4) / this.sumNutrition("kcal")) * 100), 0), 100);

  carbPercent = () =>
    Math.min(Math.max(Math.round(((this.sumNutrition("carb") * 4) / this.sumNutrition("kcal")) * 100), 0), 100);

  CalcCarbPercent = () =>
    this.carbPercent() > 15
      ? Math.min(Math.max(100 - (this.fatPercent() + this.proteinPercent()), 0), 100)
      : this.carbPercent();

  addProgressBars = () => {
    ProgressBar("#mealDetailsInfoCrabsPercent", {
      input: this.CalcCarbPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    ProgressBar("#mealDetailsInfoProteinPercent", {
      input: this.proteinPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    ProgressBar("#mealDetailsInfoFatPercent", {
      input: this.fatPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    const carbsTitle = document.createElement("p");
    const proteinTitle = document.createElement("p");
    const fatTitle = document.createElement("p");
    carbsTitle.classList.add("mealDetailsPercentTitles");
    proteinTitle.classList.add("mealDetailsPercentTitles");
    fatTitle.classList.add("mealDetailsPercentTitles");
    carbsTitle.innerHTML = "الكربوهيدرات";
    proteinTitle.innerHTML = "البروتين";
    fatTitle.innerHTML = "الدهون";
    document.getElementById("mealDetailsInfoCrabsPercent").append(carbsTitle);
    document.getElementById("mealDetailsInfoProteinPercent").append(proteinTitle);
    document.getElementById("mealDetailsInfoFatPercent").append(fatTitle);
  };

  backButtonHandle = () => window.history.back();

  render() {
    return (
      <div id="mealDetailsPage">
        <header id="mealDetailsPageHeader">
          <svg
            id="mealDetailsPageBack"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            onClick={this.backButtonHandle}
          >
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
          <p id="mealDetailsPageHeaderTitle">المعلومات الغذائية للوجبة</p>
        </header>
        <p id="mealDetailsKcal">
          {Math.round(this.sumNutrition("kcal"))}
          <span>سعرة حرارية</span>
        </p>
        <div id="mealDetailsInfoPercentContainer">
          <div id="mealDetailsInfoCrabsPercent"></div>
          <div id="mealDetailsInfoProteinPercent"></div>
          <div id="mealDetailsInfoFatPercent"></div>
        </div>
        <div id="mealDetailsNutritionInfoDetail">
          <div id="mealDetailsNutritionInfoTitles">
            <p className="mealDetailsNutritionInfoMain">البروتين</p>
            <p className="mealDetailsNutritionInfoMain">الكربوهيدرات</p>
            <p className="mealDetailsNutritionInfoSub">الألياف</p>
            <p className="mealDetailsNutritionInfoSub">السكر</p>
            <p className="mealDetailsNutritionInfoMain">الدهون</p>
            <p className="mealDetailsNutritionInfoSub">الدهون المشبعة</p>
            <p className="mealDetailsNutritionInfoSub">الدهون الغير مشبعة</p>
            <p className="mealDetailsNutritionInfoMain">آخر</p>
            <p className="mealDetailsNutritionInfoSub">الكوليسترول</p>
            <p className="mealDetailsNutritionInfoSub">الصوديوم</p>
            <p className="mealDetailsNutritionInfoSub">البوتاسيوم</p>
          </div>
          <div id="mealDetailsNutritionInfoValues">
            <p className="mealDetailsNutritionInfoMain">{Number(this.sumNutrition("protein").toFixed(1))} غ</p>
            <p className="mealDetailsNutritionInfoMain">{Number(this.sumNutrition("carb").toFixed(1))} غ</p>
            <p className="mealDetailsNutritionInfoSub">{Number(this.sumNutrition("fiber").toFixed(1))} غ</p>
            <p className="mealDetailsNutritionInfoSub">{Number(this.sumNutrition("sugar").toFixed(1))} غ</p>
            <p className="mealDetailsNutritionInfoMain">{Number(this.sumNutrition("fat").toFixed(1))} غ</p>
            <p className="mealDetailsNutritionInfoSub">{Number(this.sumNutrition("saturatedFat").toFixed(2))} غ</p>
            <p className="mealDetailsNutritionInfoSub">{Number(this.sumNutrition("unsaturatedFat").toFixed(2))} غ</p>
            <p className="mealDetailsNutritionInfoMain" style={{ visibility: "hidden" }}>
              ......
            </p>
            <p className="mealDetailsNutritionInfoSub">{this.sumNutrition("cholesterol").toFixed(0)} ملغ</p>
            <p className="mealDetailsNutritionInfoSub">{this.sumNutrition("sodium").toFixed(0)} ملغ</p>
            <p className="mealDetailsNutritionInfoSub">{this.sumNutrition("potassium").toFixed(0)} ملغ</p>
          </div>
        </div>
      </div>
    );
  }
}
